<template>
<div class="card-wrap" v-bind:class="cardClasses">
    <div class="last-played" :class="Color">
        <Card v-if="LastPlayedCard !== null" :CardData="LastPlayedCard" />
    </div>
    <div class="cards" v-bind:class="[Player === 'P1' ? 'hand-p1' : 'hand-p2']" >
        <Card @clicked="onClickChild" v-for="item in items" :key="item.id" v-bind:CardData="item"/>
    </div>
    <button v-on:click="skipTurn(true)">Skip</button>
    <button v-on:click="testPlayer()">Test Player</button>
    <div class="not-ready-box">
        Waitng for another player...
    </div>
</div>
</template>

<script>
import Card from '@/components/Cards/Card.vue';
import io from 'socket.io-client';

const socket = io();

export default {
    name: 'Hand',
    data() {
        return {
            items: [
            ],
            itemsP1: [
            ],
            itemsP2: [
            ],
            SkipCard: {
                id: 0,
                name: 'Player skip Turn',
                cost: 4,
                currency: '',
                effect: 'skip',
                target: [],
                effectCount: 8,
                desc: 'Player skip Turn',
                img: '',
            },
            MyPlayer: 'P1',
            BoardReady: false,
            LastPlayedCard: null,
            Color: null,
        };
    },
    created() {
        socket.on('receiveMessage', (obj) => this.socketTurn(obj));

        socket.on('allMessages', (objs) => {
            // const myJSON = JSON.stringify(objs);
            // alert(objs[0].login);
            if (objs.length >= 1 && objs.length < 3 && objs[0].login === 'start') {
                this.MyPlayer = 'P2';
                 /* eslint-disable */
                socket.emit('sendMessage', { 'login': 'ready' , 'msg': 'players is ready' });
                 /* eslint-endable */
            }
        });
        /* eslint-disable */
        socket.emit('sendMessage', { 'login': 'start' , 'msg': 'player is loggged' });
         /* eslint-endable */
    },
    mounted() {
        let pack1 = this.$store.getters.packp1;
        let pack2 = this.$store.getters.packp2;
        pack1 = pack1.slice(0, 7);
        pack2 = pack2.slice(0, 7);
        this.items = pack1;
        this.itemsP1 = pack1;
        this.itemsP2 = pack2;
        pack1 = this.$store.getters.packp1;
        pack2 = this.$store.getters.packp2;
        pack1 = pack1.slice(7, pack1.length);
        pack2 = pack2.slice(7, pack2.length);
        this.$store.commit('addPack1', pack1);
        this.$store.commit('addPack2', pack2);
    },
    computed: {
        Player() {
            return this.$store.getters.isPlayer;
        },
        cardClasses() {
            let cardClasses = '';
            if(this.BoardReady) {
                cardClasses += ' game-ready';
            }
            if(this.MyPlayer != this.Player){
                cardClasses += ' enemy-turn';
            }
            return cardClasses;
        }
    },
    methods: {
        testPlayer() {
            alert(`MyPlayer is ${this.MyPlayer}`);
        },
        socketTurn(obj) {
            if(obj.login === 'start') {
                return;
            } else if (obj.login === 'ready') {
                 alert(`game is ready for ${this.MyPlayer}`);
                 this.BoardReady = true;
            } else if (obj.login != this.MyPlayer && 'id' in obj.msg) {
                console.log(obj.msg);
                this.LastPlayedCard = obj.msg;
                this.Color = obj.login;
                if (this.Player === 'P1') {
                    this.items = this.itemsP1;
                } else {
                    this.items = this.itemsP2;
                }
                this.$store.commit('increment', obj.msg);
                this.$store.commit('playerChange');
            }
        },
        skipTurn() {
            this.$store.commit('increment', this.SkipCard);
            this.$store.commit('playerChange');
            this.onClickChild(this.SkipCard);
        },
        onClickChild(playedCard) {
            this.LastPlayedCard = playedCard;
            this.Color = this.MyPlayer; 
            if (this.Player === 'P1') {
                this.cardReplace(playedCard, 2);
                this.items = this.itemsP1;
            } else {
                this.cardReplace(playedCard, 1);
                this.items = this.itemsP2;
            }
            /* eslint-disable */
            socket.emit('sendMessage', { 'login': this.MyPlayer , 'msg': playedCard });
            /* eslint-endable */
        },
        cardReplace(playedCard, playerNum) {
            const itemsString = `itemsP${playerNum}`;
            const packString = `packp${playerNum}`;
            const storeMethod = `addPack${playerNum}`;

            this.[itemsString] = this.[itemsString].filter((cards) => cards.id !== playedCard.id);
            let pack1 = this.$store.getters.[packString];
            this.[itemsString].push(pack1[0]);
            pack1.push(playedCard);
            pack1 = pack1.filter((cards) => cards.id !== pack1[0].id);
            this.$store.commit(storeMethod, pack1);
        },
    },
    components: {
        Card,
    },

};
</script>

<style lang="less">
.cards {
    display: flex;
    justify-content: center;
}
.last-played {
    &.P2 .card{
        background-color: blue;
    }
    &.P1 .card{
        background-color: red;
    }
}
</style>
<style scoped lang="less">
.last-played {
    position: absolute;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 2%;
}
.not-ready-box {
    position: absolute;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.5);
    font-size: 40px;
    width: 100%;
    height: 100%;
}
.game-ready {
    .not-ready-box {
        display: none;
    }
}
.hand-p1 {
    .card {
        background: red;
        &::before {
            background: red; 
        }
    }
}
.hand-p2 {
    .card {
        background: blue;
        &::before {
           background: blue; 
        }
    }
}
.enemy-turn {
    pointer-events: none;
    .card {
        &.disable {
            opacity: 1;
        }
        &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
        }
    }
}

</style>
